import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import request from 'superagent';
import ReactHighcharts from 'react-highcharts';

class App extends Component {

  constructor() {
    super()
    this.state = {data: [], details: [], filteredData: [], showDetails: false}
  }

  componentDidMount = () => {

    request
      .get('http://localhost:8080/price')
      .then((res) => {
        this.setState({data: res.body,
          filteredData: res.body})
      })
  }

  search = (searchText) => {
    this.setState({filteredData: this.state.data.filter((v) => v.name.substr(0, searchText.length).toUpperCase() == searchText.toUpperCase() )})
  }

  showDetails = (coin) => {

    request
      .get('http://localhost:8080/history/'+coin)
      .then((res) => {
        this.setState({details: res.body,
          showDetails: true})
      })

  } 

  render() {

    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">CryptoWatcher</h1>
        </header>
        <main>


        <label htmlFor="search">Search</label>
          <input className="searchbar" id="search" type="text" onChange={(e) => this.search(e.target.value)}/>


        <table align="center" border="1">
          <tbody>
          <tr>
            <th>Currency</th>
            <th>Price</th>
            <th>Details</th>
          </tr>
          
          {this.state.filteredData.map((point, i) =>
            <tr key={i}>
              <td>{point.name}</td> 
              <td>{point.price}</td>
              <td><button onClick={() => this.showDetails(point.name)}>View details</button></td>
            </tr>
          )}
          </tbody>
        </table>

       </main>
       <br/>
      <br/>


      {this.state.showDetails && 
        <table align="center" border="1">
          <tbody>
          <tr>
            <th>Date</th>
            <th>Price</th>
            <th>txVolume</th>
          </tr>
          {this.state.details.map((point, i) => 
            <tr key={i}>
              <td>{point.date}</td> 
              <td>{point.price}</td>
              <td>{point.txVolume}</td>
            </tr>
          )}
          </tbody>
        </table>}
      </div>
    );
  }
}

export default App;


